import Image from "next/image";
import { useState, useEffect } from "react";

const DealsSection = () => {
  const [timeLeft, setTimeLeft] = useState({
    days: 4,
    hours: 13,
    minutes: 34,
    seconds: 56,
  });

  // Countdown Timer Logic
  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft((prevTime) => {
        const updatedTime = { ...prevTime };
        if (updatedTime.seconds > 0) {
          updatedTime.seconds -= 1;
        } else {
          updatedTime.seconds = 59;
          if (updatedTime.minutes > 0) {
            updatedTime.minutes -= 1;
          } else {
            updatedTime.minutes = 59;
            if (updatedTime.hours > 0) {
              updatedTime.hours -= 1;
            } else {
              updatedTime.hours = 23;
              if (updatedTime.days > 0) {
                updatedTime.days -= 1;
              }
            }
          }
        }
        return updatedTime;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const deals = [
    {
      title: "Smart watches",
      image: "/images/smartwatches.jpg",
      discount: "-25%",
    },
    {
      title: "Laptops",
      image: "/images/laptops.jpg",
      discount: "-15%",
    },
    {
      title: "GoPro cameras",
      image: "/images/cameras.jpg",
      discount: "-40%",
    },
    {
      title: "Headphones",
      image: "/images/headphones.jpg",
      discount: "-25%",
    },
    {
      title: "Canon cameras",
      image: "/images/canon.jpg",
      discount: "-25%",
    },
  ];

  return (
    <div className="bg-white shadow-md rounded-lg p-6">
      <div className="flex items-center justify-between mb-4">
        <div>
          <h2 className="text-xl font-bold">Deals and offers</h2>
          <p className="text-sm text-gray-500">Hygiene equipments</p>
        </div>
        <div className="flex gap-2 text-center text-white">
          <div className="bg-gray-800 px-3 py-1 rounded">
            <p className="text-lg font-bold">{timeLeft.days}</p>
            <p className="text-xs">Days</p>
          </div>
          <div className="bg-gray-800 px-3 py-1 rounded">
            <p className="text-lg font-bold">{timeLeft.hours}</p>
            <p className="text-xs">Hour</p>
          </div>
          <div className="bg-gray-800 px-3 py-1 rounded">
            <p className="text-lg font-bold">{timeLeft.minutes}</p>
            <p className="text-xs">Min</p>
          </div>
          <div className="bg-gray-800 px-3 py-1 rounded">
            <p className="text-lg font-bold">{timeLeft.seconds}</p>
            <p className="text-xs">Sec</p>
          </div>
        </div>
      </div>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
        {deals.map((deal, index) => (
          <div
            key={index}
            className="flex flex-col items-center text-center border p-4 rounded shadow-sm"
          >
            <Image
              src={deal.image}
              alt={deal.title}
              width={100}
              height={100}
              className="object-cover rounded"
            />
            <h3 className="mt-2 text-sm font-bold">{deal.title}</h3>
            <p className="text-red-500 mt-1">{deal.discount}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default DealsSection;

